import React, { PureComponent } from 'react'
import { Link as ScrollLink } from 'react-scroll';

import pages from '../pages.json';
import headerlogo from './headerlogo.svg';

import Box from '../../components/Box';
import Flex from '../../components/Flex';
import BackgroundImage from '../../components/BackgroundImage';
import theme from '../../components/ThemeProvider/theme';
import Text from '../../components/Text';

class Header extends PureComponent {
  state = {
    nowPage: false
  };

  setActive = (value) => this.setState({ focus: value });

  render() {
    const { nowPage } = this.state;
    return (
      <Flex
        bg="white"
        position="fixed"
        left="0"
        right="0"
        justifyContent="space-between"
        px="1em"
        py="1em"
        height={theme.headerHeight}
        alignItems="center"
        zIndex={99}
        boxShadow="0px 4px 6px rgba(0,0,0,0.15)"
      >
        <Box maxWidth="5em" width="8%">
          <ScrollLink
            spy
            smooth
            to="intro"
            onSetActive={() => this.setState({ nowPage: 'intro' })}
            style={{ cursor: 'pointer' }}
          >
            <BackgroundImage src={headerlogo} ratio={71.95 / 126.41} />
          </ScrollLink>
        </Box>
        <Flex>
          {pages.map((page, index) => {
            const active = nowPage === index;
            return (
              <Flex key={index}>
                <ScrollLink
                  smooth
                  spy
                  to={page.to}
                  onSetActive={() => this.setState({ nowPage: index })}
                  offset={-80}
                >
                  <Box
                    color={active ? 'black' : 'gray'}
                    onClick={() => this.setActive(index)}
                    borderBottom="3px solid"
                    borderColor={active ? 'yellow' : 'transparent'}
                    pb="0.25em"
                    fontWeight={active ? 'bold' : 'normal'}
                    fontSize={['0.8em', null, '1em']}
                  >
                    {page.label}
                  </Box>
                </ScrollLink>
              {index < pages.length - 1 && (<Text color="gray" mx="0.5em" lineHeight="1">|</Text>)}
              </Flex>
            );
          })}
        </Flex>
      </Flex>
    )
  }
};

export default Header;
